@use 'base/variables' as v;
@use 'base/mixins' as m;
@include m.tablet {
    .seccion-inferior {
        display: grid;
        grid-template-columns: 2fr 1fr;
        column-gap: 2rem;
    }
}
.blog {
    width: 50%;
    margin: 0 auto;
    a h2 {
        color: v.$negro;
    }
}
.entrada-blog {
    margin-bottom: 2rem;

    @include m.tablet {
        display: grid;
        grid-template-columns: 1fr 2fr;
        column-gap: 2rem;
    }
    &:last-of-type {
        margin-bottom: 0;
    }
}

.informacion-meta {
    span {
        color: v.$azul;
    }
}

.texto-entrada {
    a {
        color: v.$negro;
        margin-top: 2rem;
        display: block;
        @include m.tablet {
            margin-top: 0;
        }
    }
    h4 {
        margin: 0;
        line-height: 1.4;
        &::after {
            content: '';
            display: block;
            width: 15rem;
            height: .5rem;
            background-color: v.$azul;
            margin-top: 1rem;
        }
    }

}